import { Button, Form, Input, Card, Divider } from "antd";
import { useAuth } from "context/auth-context";
import React, { useState } from "react";
import styled from "@emotion/styled";
import bgImg from "assets/background.jpg";
import { useNavigate } from "react-router-dom";

export default function LoginScreen() {
  const { login, register } = useAuth();
  const [isRegister, setIsRegister] = useState(false);
  const navigate = useNavigate();

  const handleSubmit = (values: { username: string; password: string }) => {
    if (isRegister) {
      register(values);
    } else {
      login(values).then(() => {
        navigate(`/projectlist`);
      });
    }
  };

  return (
    <Container>
      <ShadowCard>
        <Form onFinish={handleSubmit} labelAlign={`right`}>
          <Form.Item
            name={`username`}
            label={`username`}
            rules={[{ required: true, message: "请输入用户名" }]}
          >
            <Input placeholder={`请输入用户名`} type="text" id="username" />
          </Form.Item>
          <Form.Item
            label={`password`}
            name={`password`}
            rules={[{ required: true, message: "请输入密码" }]}
          >
            <Input placeholder="{`请输入密码`}" type="password" id="password" />
          </Form.Item>
          <Form.Item>
            <Button htmlType={"submit"} type={"primary"}>
              {isRegister ? "注册" : "登录"}
            </Button>
          </Form.Item>
        </Form>
        <Divider />
        <Button type={"link"} onClick={() => setIsRegister(!isRegister)}>
          {isRegister ? "已经注册，去登陆" : "还没有注册？去注册！"}
        </Button>
      </ShadowCard>
    </Container>
  );
}

const Container = styled.div`
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
  background: url(${bgImg}) no-repeat center center/100%;
`;

const ShadowCard = styled(Card)`
  width: 40rem;
  min-height: 56rem;
  padding: 3.2rem;
  border-radius: 0.3rem;
  background-color: rgba(78, 113, 242, 0.8);
  box-sizing: border-box;
  box-shadow: rgba(0, 0, 0, 1) 0 0 10px;
  text-align: center;
`;
